import React,{useState} from 'react'
import Modal from './modal'
import vv from '../assets/vv.jpeg'
import hari from '../assets/hari.jpeg'
import sai from '../assets/sairamchat.jpeg'
import kbh from '../assets/kbh.jpeg'
import mosque from '../assets/mosquerd.jpeg'
import chetty from '../assets/chettys.png'


const restraunts = {
  data:[
      {
  "_id": {
      "$oid": "637a3a14e5aca6af9bd28ca3"
  },
  "imgSrc":vv,
  "place": "Food Street",
  "location": "V V Puram",
  "cost": "200 INR"
}, {
  "_id": {
      "$oid": "637a3a14e5aca6af9bd28ca4"
  },
  "imgSrc":hari,
  "place": "Hari Super Sandwich",
  "location": "Jayanagar",
  "cost": "150 INR"
}, {
  "_id": {
      "$oid": "637a3a14e5aca6af9bd28ca5"
  },
  "imgSrc":sai,
  "place": "Sri Sairam's Chats and Juice",
  "location": "Malleshwaram",
  "cost": "150 INR"
}, {
  "_id": {
      "$oid": "637a3a14e5aca6af9bd28ca6"
  },
  "imgSrc":kbh,
  "place": "Karnataka Bhel House",
  "location": "Bull Temple Road",
  "cost": "150 INR"
}, {
  "_id": {
      "$oid": "637a3a14e5aca6af9bd28ca7"
  },
  "imgSrc":mosque,
  "place": "Mosque Road",
  "location": "Mosque Road",
  "cost": "150 INR"
}, {
  "_id": {
      "$oid": "637a3a14e5aca6af9bd28ca8"
  },
  "imgSrc":chetty,
  "place": "Chetty's Corner",
  "location": "Kumara Park West",
  "cost": "200 INR"
}]}


function chat() {
  const [model,setModel] = useState(false);
    const [tempdata,setTempdata] = useState([]);

    const getData = (img,place,location,cost) => {
        let tempData = [img,place,location,cost];
        setTempdata(item => [1, ...tempData]);

        return setModel(true);
    }
  return (
    <>
        <section className='py-4 py-lg-5 container'>
                <div className='row justify-content-center align-item-center'>
                    {restraunts.data.map( (item,index)=>{
                        return(
                            <div className='col-11 col-md-6 col-lg-3 mx-0 mb-4' key={index}>
                        <div className="card p-0 overflow-hidden h-100 shadow">
                            <img src={item.imgSrc} className="card-img-top" alt="..."/>
                                <div className="card-body">
                                    <h5 className="card-title">{item.place}</h5>
                                    <p className="card-text">Location: {item.location}</p>
                                    <p className="card-text">Cost for two people: {item.cost}</p>
                                    <button className="btn btn-primary"
                                    onClick={() => getData(item.imgSrc,item.place,item.location,item.cost)}
                                    >Explore</button>
                                </div>
                        </div>
                    </div>
                        )
                    })}
                    
                </div>
            </section>
            <section>
                {
                    model === true? <Modal img={tempdata[1]} place={tempdata[2]} location={tempdata[3]} cost={tempdata[4]} hide={()=>setModel(false)}/> : ''

                }
            </section>
    </>
  )
}

export default chat