import React from 'react';
import { Link } from 'react-router-dom'
import '../styles/cuisines.css'
import burger from '../assets/burger.jpg'
import pizza from '../assets/it.jpg'
import thai from '../assets/thai.jpg'
import indian from '../assets/ind-meal.jpg'
import chat from '../assets/chat.jpeg'
import sweet from '../assets/dessert.webp'

let obj=[{
    "img": pizza,
    "cuisine":"Italian",
    "link": "/italian"
    },
    {
      "img": thai,
      "cuisine":"Thai",
      "link": "/thai"
    },
    {
      "img": indian,
      "cuisine":"Indian",
      "link": "/indian"
    },
    {
      "img": chat,
      "cuisine":"Street-Food",
      "link": "/chat"
    },
    {
      "img": burger,
      "cuisine":"Burger",
      "link": "/burgers"
    },
    {
      "img": sweet,
      "cuisine":"Desserts",
      "link": "/dessert"
    }]

class Cards extends React.Component{
    render(){
        return(
            <div className="columns">
                    <Link to={this.props.link}>
                    <div className="card">
                    <img className="card-img" src={this.props.image}  />
                    <h2>{this.props.value}</h2>
                    </div>
                    </Link>
            </div>
        )
    }
}

function List(){

    const Listitems=obj.map(
      (element)=> {
        return(
          <div>
          <Cards image={element.img} value={element.cuisine} link={element.link}/>
          </div>
            );
          }
        )
        
    return(
      <div>
        {Listitems}
      </div>
    );
}

export default List;
