import React,{useState} from 'react'
import Modal from './modal'
import basil from '../assets/thaibasil.jpeg'
import lan from '../assets/lanthai.jpeg'
import bo from '../assets/botai.jpeg'
import phra from '../assets/phra.jpeg'
import misu from '../assets/misu.jpeg'
import vegan from '../assets/thaivegan.png'

const restraunts = {
  data:[
      {
  "_id": {
    "$oid": "637a3eefe5aca6af9bd28cbb"
  },
  "imgSrc":basil,
  "place": "Thai Basil",
  "location": "Koramangala",
  "cost": "1200 INR"
}, {
  "_id": {
    "$oid": "637a3eefe5aca6af9bd28cbc"
  },
  "imgSrc":lan,
  "place": "Lan Thai",
  "location": "New BEL Road",
  "cost": "800 INR"
}, {
  "_id": {
    "$oid": "637a3eefe5aca6af9bd28cbd"
  },
  "imgSrc":bo,
  "place": "Bo Tai",
  "location": "Lavelle Road",
  "cost": "2800 INR"
}, {
  "_id": {
    "$oid": "637a3eefe5aca6af9bd28cbe"
  },
  "imgSrc":phra,
  "place": "Phra Nakhon",
  "location": "Koramangala",
  "cost": "400 INR"
}, {
  "_id": {
    "$oid": "637a3eefe5aca6af9bd28cbf"
  },
  "imgSrc":misu,
  "place": "Misu",
  "location": "St.Marks Road",
  "cost": "1700 INR"
}, {
  "_id": {
    "$oid": "637a3eefe5aca6af9bd28cc0"
  },
  "imgSrc":vegan,
  "place": "The Thai Vegan Kitchen",
  "location": "Bachammal Road",
  "cost": "500 INR"
}]}

function thai() {
  const [model,setModel] = useState(false);
    const [tempdata,setTempdata] = useState([]);

    const getData = (img,place,location,cost) => {
        let tempData = [img,place,location,cost];
        setTempdata(item => [1, ...tempData]);

        return setModel(true);
    }
  return (
    <>
        <section className='py-4 py-lg-5 container'>
                <div className='row justify-content-center align-item-center'>
                    {restraunts.data.map( (item,index)=>{
                        return(
                            <div className='col-11 col-md-6 col-lg-3 mx-0 mb-4' key={index}>
                        <div className="card p-0 overflow-hidden h-100 shadow">
                            <img src={item.imgSrc} className="card-img-top" alt="..."/>
                                <div className="card-body">
                                    <h5 className="card-title">{item.place}</h5>
                                    <p className="card-text">Location: {item.location}</p>
                                    <p className="card-text">Cost for two people: {item.cost}</p>
                                    <button className="btn btn-primary"
                                    onClick={() => getData(item.imgSrc,item.place,item.location,item.cost)}
                                    >Explore</button>
                                </div>
                        </div>
                    </div>
                        )
                    })}
                    
                </div>
            </section>
            <section>
                {
                    model === true? <Modal img={tempdata[1]} place={tempdata[2]} location={tempdata[3]} cost={tempdata[4]} hide={()=>setModel(false)}/> : ''
                }
            </section>        
    </>
  )
}

export default thai