import React,{useState} from 'react'
import Modal from './modal'
import cor from '../assets/cornerhouse.png'
import glens from '../assets/glens.jpeg'
import lake from '../assets/lakeview.jpeg'
import art from '../assets/artofdelight.jpeg'
import liliyum from '../assets/liliyum.jpeg'
import desert from '../assets/deserthouse.jpeg'

const restraunts = {
  data:[
      {
    "_id": {
      "$oid": "637a40c7e5aca6af9bd28cc4"
    },
    "imgSrc":cor,
    "place": "Corner House",
    "location": "Kanakapura Road",
    "cost": "300 INR"
  },{
    "_id": {
      "$oid": "637a40c7e5aca6af9bd28cc5"
    },
    "imgSrc":glens,
    "place": "Glen's Bakehouse",
    "location": "Jayanagar",
    "cost": "800 INR"
  },{
    "_id": {
      "$oid": "637a40c7e5aca6af9bd28cc6"
    },
    "imgSrc":lake,
    "place": "Lakeview Milkbar",
    "location": "M.G.Road",
    "cost": "500 INR"
  },{
    "_id": {
      "$oid": "637a40c7e5aca6af9bd28cc7"
    },
    "imgSrc":art,
    "place": "Art of Delight",
    "location": "Residency Road",
    "cost": "300 INR"
  },{
    "_id": {
      "$oid": "637a40c7e5aca6af9bd28cc8"
    },
    "imgSrc":liliyum,
    "place": "Liliyum Patisserie",
    "location": "Bommanahalli",
    "cost": "400 INR"
  },{
    "_id": {
      "$oid": "637a40c7e5aca6af9bd28cc9"
    },
    "imgSrc":desert,
    "place": "The Dessert House",
    "location": "Shantinagar",
    "cost": "350 INR"
  }]}

function dessert() {
  const [model,setModel] = useState(false);
    const [tempdata,setTempdata] = useState([]);

    const getData = (img,place,location,cost) => {
        let tempData = [img,place,location,cost];
        setTempdata(item => [1, ...tempData]);

        return setModel(true);
    }
  return (
    <>
        <section className='py-4 py-lg-5 container'>
                <div className='row justify-content-center align-item-center'>
                    {restraunts.data.map( (item,index)=>{
                        return(
                            <div className='col-11 col-md-6 col-lg-3 mx-0 mb-4' key={index}>
                        <div className="card p-0 overflow-hidden h-100 shadow">
                            <img src={item.imgSrc} className="card-img-top" alt="..."/>
                                <div className="card-body">
                                    <h5 className="card-title">{item.place}</h5>
                                    <p className="card-text">Location: {item.location}</p>
                                    <p className="card-text">Cost for two people: {item.cost}</p>
                                    <button className="btn btn-primary"
                                    onClick={() => getData(item.imgSrc,item.place,item.location,item.cost)}
                                    >Explore</button>
                                </div>
                        </div>
                    </div>
                        )
                    })}
                    
                </div>
            </section>
            <section>
                {
                    model === true? <Modal img={tempdata[1]} place={tempdata[2]} location={tempdata[3]} cost={tempdata[4]} hide={()=>setModel(false)}/> : ''
                }
            </section>        
    </>
  )
}

export default dessert