import React,{useState} from 'react'
import Modal from './modal'
import Toscano from '../assets/toscano.jpeg'
import Venezia from '../assets/venezia.jpeg'
import Chianti from '../assets/chianti.jpg'
import Italy from '../assets/littleitaly.jpeg'
import Napoli from '../assets/napoli.jpeg'
import Bologna from '../assets/bologna.jpeg'

const restraunts = {
  data:[
      {
    "_id": {
      "$oid": "637a3499e5aca6af9bd28c74"
    },
    "imgSrc":Toscano,
    "place": "Toscano",
    "location": "UB City",
    "cost": "1500 INR"
  },{
    "_id": {
      "$oid": "637a3499e5aca6af9bd28c75"
    },
    "imgSrc":Venezia,
    "place": "Venezia",
    "location": "Jayanagar",
    "cost": "900 INR"
  },{
    "_id": {
      "$oid": "637a3499e5aca6af9bd28c76"
    },
    "imgSrc":Chianti,
    "place": "Chianti",
    "location": "Indiranagar",
    "cost": "2000 INR"
  },{
    "_id": {
      "$oid": "637a3499e5aca6af9bd28c77"
    },
    "imgSrc":Italy,
    "place": "Little Italy",
    "location": "Jayanagar",
    "cost": "1400 INR"
  },{
    "_id": {
      "$oid": "637a3499e5aca6af9bd28c78"
    },
    "imgSrc":Napoli,
    "place": "Napoli Italian Bistro",
    "location": "Sarjapur Road",
    "cost": "1200 INR"
  },{
    "_id": {
      "$oid": "637a38b3e5aca6af9bd28c9f"
    },
    "imgSrc":Bologna,
    "place": "Bologna",
    "location": "Indiranagar",
    "cost": "1600 INR"
  }]}

  function italian() {
    const [model,setModel] = useState(false);
      const [tempdata,setTempdata] = useState([]);
  
      const getData = (img,place,location,cost) => {
          let tempData = [img,place,location,cost];
          setTempdata(item => [1, ...tempData]);
  
          return setModel(true);
      }
    return (
      <>
          <section className='py-4 py-lg-5 container'>
                  <div className='row justify-content-center align-item-center'>
                      {restraunts.data.map( (item,index)=>{
                          return(
                              <div className='col-11 col-md-6 col-lg-3 mx-0 mb-4' key={index}>
                          <div className="card p-0 overflow-hidden h-100 shadow">
                              <img src={item.imgSrc} className="card-img-top" alt="..."/>
                                  <div className="card-body">
                                      <h5 className="card-title">{item.place}</h5>
                                      <p className="card-text">Location: {item.location}</p>
                                      <p className="card-text">Cost for two people: {item.cost}</p>
                                      <button className="btn btn-primary"
                                      onClick={() => getData(item.imgSrc,item.place,item.location,item.cost)}
                                      >Explore</button>
                                  </div>
                          </div>
                      </div>
                          )
                      })}
                      
                  </div>
              </section>
              <section>
                  {
                      model === true? <Modal img={tempdata[1]} place={tempdata[2]} location={tempdata[3]} cost={tempdata[4]} hide={()=>setModel(false)}/> : ''
                  }
              </section>        
      </>
    )
  }

export default italian