import React,{useState} from 'react'
import Modal from './modal'
import truffles from '../assets/truffles.png'
import boss from '../assets/boss.jpg'
import leons from '../assets/leon.jpeg'
import brown from '../assets/brownburger.png'
import simon from '../assets/simons.jpeg'
import mcd from '../assets/mcd.jpg'

const restraunts = {
  data:[
      {
    "_id": {
      "$oid": "637a3c1ce5aca6af9bd28cb2"
    },
    "imgSrc":truffles,
    "place": "Truffles",
    "location": "Koramangala",
    "cost": "900 INR"
  },{
    "_id": {
      "$oid": "637a3c1ce5aca6af9bd28cb3"
    },
    "imgSrc":boss,
    "place": "Boss Burger",
    "location": "Indiranagar",
    "cost": "500 INR"
  },{
    "_id": {
      "$oid": "637a3c1ce5aca6af9bd28cb4"
    },
    "imgSrc":leons,
    "place": "Leon's Burger and Wings",
    "location": "HSR Layout",
    "cost": "400 INR"
  },{
    "_id": {
      "$oid": "637a3c1ce5aca6af9bd28cb5"
    },
    "imgSrc":brown,
    "place": "Brown Burger Co.",
    "location": "Jayanagar",
    "cost": "450 INR"
  },{
    "_id": {
      "$oid": "637a3c1ce5aca6af9bd28cb6"
    },
    "imgSrc":simon,
    "place": "Simon Burgers",
    "location": "Kammanahalli",
    "cost": "500 INR"
  },{
    "_id": {
      "$oid": "637a3c1ce5aca6af9bd28cb7"
    },
    "imgSrc":mcd,
    "place": "McDonald's",
    "location": "Mantri Square Mall",
    "cost": "400 INR"
  }]}


function burgers() {
  const [model,setModel] = useState(false);
    const [tempdata,setTempdata] = useState([]);

    const getData = (img,place,location,cost) => {
        let tempData = [img,place,location,cost];
        setTempdata(item => [1, ...tempData]);

        return setModel(true);
    }
  return (
    <>
        <section className='py-4 py-lg-5 container'>
                <div className='row justify-content-center align-item-center'>
                    {restraunts.data.map( (item,index)=>{
                        return(
                            <div className='col-11 col-md-6 col-lg-3 mx-0 mb-4' key={index}>
                        <div className="card p-0 overflow-hidden h-100 shadow">
                            <img src={item.imgSrc} className="card-img-top" alt="..."/>
                                <div className="card-body">
                                    <h5 className="card-title">{item.place}</h5>
                                    <p className="card-text">Location: {item.location}</p>
                                    <p className="card-text">Cost for two people: {item.cost}</p>
                                    <button className="btn btn-primary"
                                    onClick={() => getData(item.imgSrc,item.place,item.location,item.cost)}
                                    >Explore</button>
                                </div>
                        </div>
                    </div>
                        )
                    })}
                    
                </div>
            </section>
            <section>
                {
                    model === true? <Modal img={tempdata[1]} place={tempdata[2]} location={tempdata[3]} cost={tempdata[4]} hide={()=>setModel(false)}/> : ''

                }
            </section>
    </>
  )
}

export default burgers