import React,{useState} from 'react'
import Modal from './modal'
import jalpaan from '../assets/jalpaan.jpg'
import mtr from '../assets/mtr.jpeg'
import paaka from '../assets/paakashala.jpeg'
import karnatic from '../assets/karnatic.jpg'
import gufha from '../assets/gufha.jpeg'
import kapoor from '../assets/kapoor.jpeg'

const restraunts = {
  data:[
      {
    "_id": {
      "$oid": "637a3850e5aca6af9bd28c92"
    },
    "imgSrc":jalpaan,
    "place": "Jalpaan",
    "location": "Jayanagar",
    "cost": "900 INR"
  },{
    "_id": {
      "$oid": "637a3850e5aca6af9bd28c93"
    },
    "imgSrc":mtr,
    "place": "MTR",
    "location": "Lalbagh",
    "cost": "600 INR"
  },{
    "_id": {
      "$oid": "637a3850e5aca6af9bd28c94"
    },
    "imgSrc":paaka,
    "place": "Paakashala",
    "location": "Kanakapura Road",
    "cost": "500 INR"
  },{
    "_id": {
      "$oid": "637a3850e5aca6af9bd28c95"
    },
    "imgSrc":karnatic,
    "place": "Karnatic",
    "location": "Church Street",
    "cost": "1000 INR"
  },{
    "_id": {
      "$oid": "637a3850e5aca6af9bd28c96"
    },
    "imgSrc":gufha,
    "place": "Gufha-The President Hotel",
    "location": "Jayanagar",
    "cost": "1200 INR"
  },{
    "_id": {
      "$oid": "637a3850e5aca6af9bd28c97"
    },
    "imgSrc":kapoor,
    "place": "Kapoor's Cafe",
    "location": "HSR Layout",
    "cost": "700 INR"
  }]}

function indian() {
  const [model,setModel] = useState(false);
    const [tempdata,setTempdata] = useState([]);

    const getData = (img,place,location,cost) => {
        let tempData = [img,place,location,cost];
        setTempdata(item => [1, ...tempData]);

        return setModel(true);
    }
  return (
    <>
        <section className='py-4 py-lg-5 container'>
                <div className='row justify-content-center align-item-center'>
                    {restraunts.data.map( (item,index)=>{
                        return(
                            <div className='col-11 col-md-6 col-lg-3 mx-0 mb-4' key={index}>
                        <div className="card p-0 overflow-hidden h-100 shadow">
                            <img src={item.imgSrc} className="card-img-top" alt="..."/>
                                <div className="card-body">
                                    <h5 className="card-title">{item.place}</h5>
                                    <p className="card-text">Location: {item.location}</p>
                                    <p className="card-text">Cost for two people: {item.cost}</p>
                                    <button className="btn btn-primary"
                                    onClick={() => getData(item.imgSrc,item.place,item.location,item.cost)}
                                    >Explore</button>
                                </div>
                        </div>
                    </div>
                        )
                    })}
                    
                </div>
            </section>
            <section>
                {
                    model === true? <Modal img={tempdata[1]} place={tempdata[2]} location={tempdata[3]} cost={tempdata[4]} hide={()=>setModel(false)}/> : ''

                }
            </section>
    </>
  )
}

export default indian