import React from 'react';
import bgimg from '../assets/bgimg.jpg'
import '../styles/Home.css'

const About = () => {
  return (
    <div className='home' style={{backgroundImage: `url(${bgimg})` }}>
      <div className='headerContainer'>
      <h1 style={{float:'right'}}>About</h1><br/><br/><br/><br/><br/>
      <h4 style={{float:'right'}}>Head Over Meals is the go to website for foodies.<br/>We suggest the top-rated restaurants/cafes
      for the most popular cuisines.<br/>The best places are picked to explore your taste buds. Don't miss out on the most authentic places!!!
      <br/>So why wait? Let's dive in!</h4>
    </div>
    </div>
  );
};
  
export default About;